* {
  margin: 0;
}

body {
  --active-link-color: #00acdc;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__icon {
  width: 40px;
  height: 40px;
}

nav {
  margin: 0;
}

.nav__items {
  align-items: center;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-content: center;
  height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--active-link-color);
}

.is-active {
  color: var(--active-link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
